<template>
	<view>
		<!--消息列表-->
		<template v-if="list.length > 0">
			<block v-for="(item,index) in list" :key="index">
				<msgList :item="item" :index="index"></msgList>
			</block>
		</template>
		<template v-else>
			<nothing></nothing>
		</template>
		
		<!--弹出层-->
		<uni-popup ref="popup" type="top">
			<view class="w-100" style="background-color: white;">
				<view class="flex align-center justify-center font-md border-bottom py-2" 
				@click="popupEvent('friend')"
				hover-class="bg-light" >
					<text class="iconfont icon-sousuo mr-2"></text> 添加好友
				</view>
				<view class="flex align-center justify-center font-md border-bottom py-2"
				 @click="popupEvent('clear')"
				 hover-class="bg-light">
					<text class="iconfont icon-shanchu mr-2"></text> 清除列表
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	const demo = [
		{
			avatar : "/static/default.jpg",
			username : "lisa",
			update_time : 1570718427,
			data : "内容内容内容内容内容内容内容内容内容内容内容",
			noread : 20
		},
		{
			avatar : "/static/default.jpg",
			username : "lisa",
			update_time : 1570718427,
			data : "内容内容内容内容内容内容内容内容内容内容内容",
			noread : 20
		},
		{
			avatar : "/static/default.jpg",
			username : "lisa",
			update_time : 1570718427,
			data : "内容内容内容内容内容内容内容内容内容内容内容",
			noread : 20
		},
		{
			avatar : "/static/default.jpg",
			username : "lisa",
			update_time : 1570718427,
			data : "内容内容内容内容内容内容内容内容内容内容内容",
			noread : 20
		}
	]
	
	import uniBadge from '../../components/uni-ui/uni-badge/uni-badge.vue'
	import msgList from '../../components/msg/msg-list.vue';
	import uniPopup from '../../components/uni-ui/uni-popup/uni-popup.vue'

	
	export default {
		components:{
			uniBadge,
			msgList,
			uniPopup
		},
		data() {
			return {
				list : []
			}
		},
		// 页面加载
		onLoad(){
			this.list = demo
		},
		// 监听下拉刷新
		onPullDownRefresh() {
			this.refresh()
		},
		// 监听原生导航栏
		onNavigationBarButtonTap(e){
			switch(e.index){
				case 0 : //左边
				uni.navigateTo({
					url:"../user-list/user-list"
				})
				this.$refs.popup.close()
					break;
				case 1 : //右边
				this.$refs.popup.open()
					break;
			}
		},
		methods: {
			// 下拉刷新
			refresh(){
				setTimeout(() => {
					this.list = demo
					// 停止下拉刷新
					uni.stopPullDownRefresh()
				},2000)
			},
			popupEvent(e){
				switch(e){
					case 'friend' : 
					uni.navigateTo({
						url:"../search/search?type=user"
					})
					break;
					case 'clear' : break;
				}
				// 关闭弹出层
				this.$refs.popup.close()
			}
		}
	}
</script>

